import { Component } from '@angular/core';
import { ViewController,IonicPage,  NavController,Events , ActionSheetController, NavParams } from 'ionic-angular';
import {   FormBuilder,  FormGroup,   Validators } from '@angular/forms';
import { Auth,NotificationProvider } from '../../../../providers';
import { TranslateService } from '@ngx-translate/core';
import { Storage } from '@ionic/storage';

@IonicPage()
@Component({
  selector: 'page-Unidadhorario3-create',
  templateUrl: 'unidadhorario3-create.html',
})
export class Unidadhorario3CreatePage {

  public form                  : FormGroup;
  username: string = "";
  public idtipo: string="";
  public idtipo2: string="";
  public idjornada: string="";
  public idnombre: string="";
  public codigo                 : any;
 
  public nombre                 : any;
  public nombre2                 : any;
  public dia                 : any;
  idempresa:any;
  public          lunes        : any;
  public            martes      : any;
  public            miercoles      : any;
  public             jueves     : any;
  public               viernes   : any;
  public               sabado   : any;
  public               domingo   : any;

    
   
  private _ID                  : String;
  public pageTitle           	: string;    

  constructor( public storage: Storage,public msj: NotificationProvider, public translateService: TranslateService,public viewCtrl: ViewController,public authService:  Auth,public navCtrl 		: NavController,  
    public navParams 	: NavParams,   private _FB          : FormBuilder,         public actionSheetCtrl: ActionSheetController, 
          public events: Events)
  {
    this. translateService.get('APPX').subscribe((value) => {
      this.idempresa=value
     
  })
     this.form = this._FB.group({
      codigo     : ['', Validators.required],
       nombre     : ['', Validators.required],   
       nombre2     : ['', Validators.required],
       dia     : ['', Validators.required],
       lunes   : [''],
       martes   : [''],
       miercoles   : [''],
       jueves   : [''],
       viernes   : [''],
       sabado   : [''],
       domingo   : ['']
      
     
      
       
     });

    
  }

  limpia(op:boolean) : void
  {
   
    this.form.controls['lunes'].setValue(op);
     this.form.controls['martes'].setValue(op);
     this.form.controls['miercoles'].setValue(op);
     this.form.controls['jueves'].setValue(op);
    this.form.controls['viernes'].setValue(op);
    this.form.controls['sabado'].setValue(op);
    this.form.controls['domingo'].setValue(op);
  }
    

 

  ngOnInit() {
    this.storage.get('username').then((value) => {
      this.username = value;
  }); 
  }


  Checkbox1(op:any)
  {
var cad=''
    if(this.lunes){cad=cad+' Lunes '}
    if(this.martes){cad=cad+' Martes '}
    if(this.miercoles){cad=cad+' Miercoles '}
    if(this.jueves){cad=cad+' Jueves '}
    if(this.viernes){cad=cad+' Viernes '}
    if(this.sabado){cad=cad+' Sabado '}
    if(this.domingo){cad=cad+' Domingo '}

    this.dia=cad;

  }
  ionViewDidLoad() : void
  {
    this.idtipo=this.navParams.data.record2.idtipo;
    this.idtipo2=this.navParams.data.record2.idtipo2;
    this.idnombre=this.navParams.data.record2.idnombre;
    this.idjornada=this.navParams.data.record2.idjornada
    if(this.navParams.get("record")==null)
    {
      this.limpia(false);   
    
      this.translateService.get('ITEM_CREATE_TITLE').subscribe((value) => {
        this.pageTitle=value;
      });
    }
    else {
       this._ID 				=	this.navParams.data.record._id;
       this.codigo		=	this.navParams.data.record.codigo;
       this.nombre		=	this.navParams.data.record.nombre;
       this.nombre2		=	this.navParams.data.record.nombre2;
       this.dia		=	this.navParams.data.record.dia;
       var dd=''
       this.limpia(false);
       dd=this.dia
        var vv=dd.split(' ')
 
    


        for (var j = 0; j < vv.length; j++)
        {
          switch (vv[j]) {
            case 'Lunes':
              this.form.controls['lunes'].setValue('true');
            break
            case 'Martes':
              this.form.controls['martes'].setValue('true');
            break
            case 'Miercoles':
              this.form.controls['miercoles'].setValue('true');
            break
            case 'Jueves':
              this.form.controls['jueves'].setValue('true');
            break
            case 'Viernes':
              this.form.controls['viernes'].setValue('true');
            break
            case 'Sabado':
              this.form.controls['sabado'].setValue('true');
            break
            case 'Domingo':
              this.form.controls['domingo'].setValue('true');
            break

            default:
      
          }
   
          
        }


     
    
       this.translateService.get('ITEM_UPDATE_TITLE').subscribe((value) => {
        this.pageTitle=value;
      });
    }
  }

  

  manageGallery() : void
  {
  let creat='Actualiza';
    if(this.navParams.get("record")==null)
    {  creat='Crea';
    }
     let   options       : any	     = { 
      idtipounidad : this.idtipo,  
      idunidadacademica : this.idtipo2,  
      idjornada : this.idjornada, 
      codigo : this.form.controls['codigo'].value,
     nombre : this.form.controls['nombre'].value,
     nombre2 : this.form.controls['nombre2'].value,
     dia : this.form.controls['dia'].value,
    
     bitacora:{idempresa:this.idempresa,idafiliado:'',email: this.username,permiso:creat,accion:creat+' horarioes calusac'}
    };
      

     if(this.navParams.get("record")==null)
     {  
          if(options){
            this.authService.createReg('',options,'unidadhorario3s').then((result) => {
              if (!this.form.valid) { return; }
              this.viewCtrl.dismiss(result);
          }, (err) => {  
            this.msj.showError3('Error',err.error);
          });
          }
     }   
     else
     {
      if(options){
        this.authService.createReg(this._ID,options,'unidadhorario3s').then((result) => {
           if (!this.form.valid) { return; }
           console.log(result)
           this.viewCtrl.dismiss(result);
       }, (err) => {
        this.msj.showError3('Error',err.error);
       });
    }}
  }

  cancel() {
    this.viewCtrl.dismiss();
  }
  done() {
    if (!this.form.valid) { return; }
    //this.viewCtrl.dismiss(this.form.value);
  }


}



